import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Modal, Tooltip } from 'antd';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common';
import { Btn, BtnAddNew, BtnExport } from 'components/common/button';
import { dataExamOrganization } from 'dump/data';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';
import { getTestOrganizings } from 'services/testOrganizing';

import FromSearch from './FormSearch';

import TeacherExam from './TeacherExam';
import { getQueryParams } from '../../../utils/urlUtil';

const ROUTER_FORM_DATA = routeConstant.TO_CHUC_THI__QUAN_LY_LICH_THI__NHAP_DU_LIEU;
class ExamOrganization extends Component {
  state = { visible: false, paramSearch: {} };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    const paramSearch = getQueryParams();
    this.setState({ paramSearch });
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    const columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'kh',
        key: 'id',
      },
      {
        title: 'Tên môn',
        dataIndex: 'tm',
        key: 'id',
      },
      {
        title: 'Đợt thi',
        dataIndex: 'dt',
        key: 'id',
      },
      {
        title: 'Lần thi',
        dataIndex: 'lt',
        key: 'id',
      },
      {
        title: 'Ngày thi',
        dataIndex: 'nt',
        key: 'id',
      },
      {
        title: 'Ca thi',
        dataIndex: 'ct',
        key: 'id',
      },
      {
        title: 'Giờ thi',
        dataIndex: 'gt',
        key: 'id',
      },
      {
        title: 'Số SV',
        dataIndex: 'ssv',
        key: 'id',
      },
      {
        title: 'Phòng thi',
        dataIndex: 'pt',
        key: 'id',
      },
      {
        title: 'Giám thị 1',
        dataIndex: 'gt1',
        key: 'id',
      },
      {
        title: 'Giám thị 2',
        dataIndex: 'gt2',
        key: 'id',
      },
      {
        title: 'Giám thị 3',
        dataIndex: 'gt3',
        key: 'id',
      },
      {
        title: 'Ghi chú',
        dataIndex: 'gc',
        key: 'id',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <Fragment>
            <ColEditDelete
              linkEdit={{
                to: {
                  pathname: ROUTER_FORM_DATA,
                  search: `?id=${record.id}`,
                },
              }}
              onConfirmDelete={() => this.handleDelete(record.id)}
            />
            <Tooltip placement="top" title="Phân GT">
              <Btn style={{ marginTop: 5 }} icon="user-o" onClick={this.showModal} />
            </Tooltip>
            <Tooltip placement="top" title="Xuất excel">
              <BtnExport style={{ marginTop: 5 }} icon="user-o" title="" onClick={this.showModal} />
            </Tooltip>
            <Tooltip placement="top" title="Xuất excel">
              <BtnExport style={{ marginTop: 5 }} icon="user-o" title="" onClick={this.showModal} />
            </Tooltip>
          </Fragment>
        ),
      },
    ];
    return (
      <Card title="Tổ chức thi">
        <CartHeadAction>
          <Link to={ROUTER_FORM_DATA}>
            <BtnAddNew />
          </Link>
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FromSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              service={getTestOrganizings}
              paramSearch={this.state.paramSearch}
              bordered
              dataSource={dataExamOrganization}
              columns={columns}
            />
          </Col>
        </Row>
        <Modal
          title="Phân giám thị"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          style={{ top: 20 }}
        >
          <TeacherExam />
        </Modal>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ExamOrganization />
  </Page>
);



